$silver: #555B6E;
$turquoise: #00A5B7;
$blue: #0046e7;
$dark-grey: #555b6e;
$grey: #F5F6F9;

$font-base: 'Work Sans', sans-serif;
$font-heading: 'Work Sans', sans-serif;
$font-secondary: 'Arial', sans-serif;

$h1-size: 52px;
$h2-size: 38px;
$h5-size: 18px;
$font-size-large: 20px;

$button-padding: 50px;

$form-color-secondary: #DADADA;
$form-color-base: #707070;

$grid-item-gutter-vertical: 10px;

$allow-icons: false;

$arrowsizes: (
  'sm': 30, 
  'md': 72,
  'mdlg': 110, 
  'lg': 180,
  'xl': 252
);

$arrowsizesrsp: (
  'sm': 30, 
  'md': 36,
  'mdlg': 110, 
  'lg': 90,
  'xl': 126
);

$theta: 0.2;

.arrow {
  width:0px;
  height:0px;
}

@mixin arrowright($size, $color)
{
  @extend .arrow;
  @include snap-up-from('md')
  {
    border-top: #{map-get($arrowsizes, $size) - map-get($arrowsizes, $size) * $theta}px solid transparent;
    border-bottom: #{map-get($arrowsizes, $size) - map-get($arrowsizes, $size) * $theta}px solid transparent;
    border-left: #{map-get($arrowsizes, $size)}px solid $color;
  }
  @include snap-down-from('md') {
    border-top: #{map-get($arrowsizesrsp, $size) - map-get($arrowsizesrsp, $size) * $theta}px solid transparent;
    border-bottom: #{map-get($arrowsizesrsp, $size) - map-get($arrowsizesrsp, $size) * $theta}px solid transparent;
    border-left: #{map-get($arrowsizesrsp, $size)}px solid $color;  
  }
}

@mixin arrowleft($size, $color)
{
  @extend .arrow;
  border-top: #{map-get($arrowsizes, $size) - map-get($arrowsizes, $size) * $theta}px solid transparent;
  border-bottom: #{map-get($arrowsizes, $size) - map-get($arrowsizes, $size) * $theta}px solid transparent;
  border-right: #{map-get($arrowsizes, $size)}px solid $color;
  @include snap-down-from('md') {
  border-top: #{map-get($arrowsizesrsp, $size) - map-get($arrowsizesrsp, $size) * $theta}px solid transparent;
  border-bottom: #{map-get($arrowsizesrsp, $size) - map-get($arrowsizesrsp, $size) * $theta}px solid transparent;
  border-right: #{map-get($arrowsizesrsp, $size)}px solid $color;
  }
}