@import './variables';

@import "~roseworx/scss/roseworx";

.f2 {
  font-family: $font-secondary;
}

.h100 {
  height:100% !important;
}

.fw600 {
  font-weight: 600 !important;
}

.arrow-r-sm
{
  @include arrowright('sm', $turquoise);
  @include snap-down-from('md')
  {
    &.rwxsx-end {
      transform: translate(0, 0) rotate(90deg) !important;
    }
  }
}

.heading-arrow {
  @include snap-up-from('md')
  {
    justify-content:flex-end;
  }
  @include snap-down-from('md')
  {
    .arrow
    {
      transform: rotate(90deg);
    }
  }
}

.rwx-form-item {
  font-size: rem(18px);
  input, textarea {
    background-color:white;
  }
  label
  {
    display: none;
  }
}


.icon {
  width:36px;
  height:36px;
  border-radius: 18px;
  @include snap-down-from('md')
  {
    width: 48px;
    height: 48px;
    border-radius: 24px;
  }
  &.icon-small {
    width:18px;
    height:18px;
    border-radius:9px;
  }
}

button, .rwx-button
{
  letter-spacing: 2px;
  font-size: rem(16px) !important;
  border-radius: 0px !important;
  @include onAccessible(true)
  {
    background-color: $white !important;
  }
  @include snap-down-from('md') {
    width: 100%;
  }
}

h2, .h2 {
  @include snap-down-from('md')
  {
    font-size: rem(26px);
    line-height: rem(32px);
  }
}

h1 {
  @include snap-down-from('md')
  {
    font-size: rem(30px);
    line-height: rem(36px);
  }
}

h5 {
  @include snap-down-from('md')
  {
    font-size: rem(16px);
  }
}

h5, button, .rwx-button {
  text-transform: uppercase;
}

h1, .h1, h2, .h2
{
  font-weight: normal !important;
}

.navtop {
  height:111px;
}

.navigation {
  position: relative;
  height:100px;
  z-index:3;
  width:100%;
  transform: translateY(-50%);
  background:transparent;
  > .rwx-split {
    position: relative;
    &:before
    {
      position:absolute;
      left:-50%;
      height:100%;
      width:100%;
      background-color: $white;
      content: "";
      @include snap-down-from('md')
      {
        display: none;
      }
    }
  }
}

.navigation-rsp
{
  max-width:450px;
  width:100%;
  position:fixed;
  top:55px;
  left:0px;
  z-index:8;
  padding:55px;
  transform: translateY(-100%);
  transition: all 0.7s $transition-base;
  &.active {
    transition: all 0.7s $transition-base;
    transform: translateY(0%);
  }
  .rwx-button {
    width:100%;
  }
}

.navtop-rsp
{
  position:fixed;
  top:0px;
  left:0px;
  width:100%;
  min-height: 55px;
  z-index:9;
}

#toggle-nav {
  cursor:pointer;
  height:55px;
  width:55px;
  display: flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  &.active {
    .hamburger:first-child {
      transform: rotate(45deg);
      margin-left:-10px;
    }
    .hamburger:nth-child(2) {
      transform: translateY(-60px);
    }
    .hamburger:last-child {
      transform: rotate(-45deg);
      margin-top:-37px;
      margin-left:-10px;
    }
  }
  .hamburger
  {
    transform-origin:right;
    transition: all 0.7s $transition-base;
    height:3px;
    width:60%;
    &:not(:first-child)
    {
      margin-top: 8px;
    }
  }
}

.find {
  @include snap-down-from('md') {
    margin-left:120px;
    margin-top:-45px;
    margin-bottom: 60px;
  }
}

.map {
  margin-top: -212px;
  &-arrow
  {
    position: absolute;
    left:0px;
    @include arrowright('md', $blue);
    z-index:4;
    top:-78px;
    &.big{
      @include arrowright('lg', $turquoise);
      z-index:3;
    }
  }
}

body {
  overflow-x:hidden;
}

.find-us-bar {
  height: 212px;
  .address {
    cursor:pointer;
    transition: all 0.7s $transition-base;
    @include snap-up-from('md')
    {
      min-width: 320px;
    }
    @include snap-down-from('md')
    {
      width:50%;
    }
    &.active {
      background: $turquoise;
      color: $white;
    }
  }
  &.rwx-split {
    padding:0px !important;
  }
}

.navigation a.contact-button
{
  position: absolute;
  top:0px;
  right: -$grid-item-gutter * 0.5;
  height:100%;
  width:175px;
  @include flex-center;
}

.cover-image {
  background-image: url('/assets/cover.png');
  min-height:700px;
  height:80vh;
  width:100%;
  background-size: cover;
  position: relative;
  background-repeat: no-repeat;

  @include snap-up-from('md')
  {
    margin-top: -100px;
  }

  @include snap-down-from('md') {
    background-position: -333px;
    min-height:500px;
    height: 70vh;
  }

  .title {
    margin-left: 10%;
    display:flex;
    flex-wrap:wrap;
    @include snap-up-from('md')
    {
      align-items: center;
      max-width: 630px;
      min-width: 400px;
    }
    @include snap-down-from('md')
    {
      width:80%;
      h1 {
        margin-top: 120px !important;
      }
    }
  }

  .tri-small, .tri-large
  {
    position: absolute;
    bottom: 0px;
    right: 0px;
  }
  .tri-small {
    @include arrowleft('xl', $turquoise);
    z-index:4;
  }
  .tri-large {
    width: 508px;
    height: 800px;
    background-size: contain;
    bottom: -400px;
    background-repeat: no-repeat;
    right: 0px;
    background-image: url('/assets/header-image.png');
    @include snap-down-from('md') {
      width:254px;
      height:400px;
      bottom: -201px;
    }
  }
}

.footer {
  min-height:325px;
  position: relative;
  .logo-footer {
    @include snap-down-from('md')
    {
      width:50%;
    }
  }
  .tri-small, .tri-large
  {
    position:absolute;
    right:0px;
    @include snap-down-from('lg') {
      display: none;
    }
  }
  .tri-small {
    z-index:4;
    top:-86px;
    @include arrowleft('mdlg', $turquoise);
  }
  .tri-large {
    background-image: url('/assets/footer-image.png');
    background-repeat: no-repeat;
    width: 410px;
    height: 410px;
    background-size: cover;
    bottom:0px;
  }
}